import React, { useEffect, useState } from "react";
import "./style.css";

const App = () => {
  const [lati, changeLati] = useState(""); //latitude
  const [long, changeLong] = useState(""); //longitude
  const [weather, changeWeather] = useState([]); //wether data
  const [tempIcon, changeTempOIcon] = useState("C"); //change degree's buuton text
  const [temp, changeTemp] = useState(""); //save values of degrees
  const [condi, changeCondi] = useState(true);

  useEffect(() => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        changeLati(position.coords.latitude);
        changeLong(position.coords.longitude);
        fetch(
          `https://weather-proxy.freecodecamp.rocks/api/current?lat=${position.coords.latitude}&lon=${position.coords.longitude}`
        )
          .then((response) => response.json())
          .then((data) => {
            changeWeather(data);
            console.log(data);
          })
          .catch((err) => console.log(err));
      });
    } else {
      console.log("not available");
    }
  }, [lati, long]);

  const handleChange = () => {
    const f = weather.main.temp * (9 / 5) + 32;
    const c = (f - 32) * (5 / 9);
    if (typeof weather.main !== "undefined") {
      changeCondi(false);
      if (tempIcon === "F") {
        changeTemp(c.toFixed(2));
        changeTempOIcon("C");
      } else {
        changeTemp(f.toFixed(2));
        changeTempOIcon("F");
      }
    }
  };


  return (
    <div className="App">
      {typeof weather.main !== "undefined" ? (
        <div className="grids">
          <div className="header">
            <h1 className="heading">Weather App</h1>
            <div>
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12 0c-4.198 0-8 3.403-8 7.602 0 4.198 3.469 9.21 8 16.398 4.531-7.188 8-12.2 8-16.398 0-4.199-3.801-7.602-8-7.602zm0 11c-1.657 0-3-1.343-3-3s1.343-3 3-3 3 1.343 3 3-1.343 3-3 3z"/></svg>
            <h1 className="location">
              {weather.name + ", " + weather.sys.country}
            </h1>
            </div>
          </div>
          <h2 className="temprature">
            {condi ? weather.main.temp : temp}&deg;
            <button onClick={handleChange}>{tempIcon}</button>
          </h2>
          <div className="weatherCondi">
            <img src={weather.weather[0].icon} alt="weather-icon" />
            <h2>{weather.weather[0].main}</h2>
          </div>
          <div className="icons_1">
            <div>
              <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path d="M19.672 7.984c.109 1.286 1.031 2.375 2.203 3.234-2.051-.277-3.885-1.373-4.834-2.768-3.646 4.06-5.446 8.694-5.543 12.668.361.053.709.123 1.041.21.836-2.892 3.039-5.155 4.477-5.783.32-1.874 2.381-2.918 3.61-2.285-.957.04-1.648.702-1.836 1.348 1.073.082 1.784.55 2.21 1.352-.734-.454-1.586-.591-2.348-.111.214.705.857 1.202 1.613 1.543-1.176.088-2.33-.305-3.024-.971-1.499.757-2.909 3.671-3.077 5.508 1.082.546 1.9 1.282 2.386 2.069h-13.1c.857-1.391 2.743-2.622 5.319-2.926.521-4.065 3.917-10.184 7.641-13.476-.752-2.166.778-6.158 5.59-6.596-2.062.979-3.406 2.625-3.266 3.719 1.688-1.844 4.11-1.547 5.266-.453-1.656-.281-2.859.422-3.641 1.641 1.812.531 2.859 1.594 3.297 3.125-1.094-1.048-2.505-1.594-3.984-1.048zm-14.65-7.984c-1.16 0-2.155.666-2.654 1.631l1.749.956c.158-.345.502-.587.905-.587.552 0 1 .448 1 1s-.448 1-1 1h-5.022v2h5.022c3.969 0 3.971-6 0-6zm.978 16c-1.16 0-2.155-.666-2.654-1.631l1.749-.956c.158.345.502.587.905.587.552 0 1-.448 1-1s-.448-1-1-1h-6v-2h6c1.656 0 3 1.344 3 3s-1.344 3-3 3zm8-10c0-1.5-.992-3-2.978-3-1.16 0-2.155.666-2.654 1.631l1.749.956c.158-.345.502-.587.905-.587.552 0 1 .448 1 1s-.448 1-1 1h-11.022v2h11.022c1.985 0 2.978-1.5 2.978-3z"/></svg>
              <h2>{(weather.wind.speed * 1.609).toFixed(1)} km/hr</h2>
            </div>
            <div>
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path d="M20 6.093l-3-3v-2.093h3v5.093zm1 11.349c.813.315 1.732.558 3 .558v2c-3.896 0-5.083-2-8.002-2-3.04 0-4.436 2-8.002 2-3.684 0-4.376-2-7.996-2v-2c1.275 0 2.217.184 3 .438v-4.438h-3l12-12 12 12h-3v5.442zm-5.002-1.442c1.269 0 2.183.255 3.002.581v-6.763l-7-6.99-7 6.912v7.543c.849.39 1.711.717 2.996.717.749 0 1.396-.124 2.004-.308v-4.692h4v3.242c.598-.14 1.242-.242 1.998-.242zm-8.002 6c-3.184 0-3.767-2-7.996-2v2c3.62 0 4.312 2 7.996 2 3.566 0 4.962-2 8.002-2 2.919 0 4.106 2 8.002 2v-2c-3.649 0-4.438-2-8.002-2-3.581 0-4.977 2-8.002 2z"/></svg>
              <h2>{weather.main.sea_level} mbar</h2>
            </div>
            <div>
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path d="M17.5 2c.827 0 1.5.673 1.5 1.5v7.525c0 1.569.514 2.287 1.411 3.05 1.01.858 1.589 2.106 1.589 3.425 0 2.481-2.019 4.5-4.5 4.5s-4.5-2.019-4.5-4.5c0-1.319.579-2.567 1.59-3.425.896-.761 1.41-1.479 1.41-3.05v-7.525c0-.827.673-1.5 1.5-1.5zm0-2c-1.933 0-3.5 1.567-3.5 3.5v7.525c0 .587-.258 1.146-.705 1.525-1.403 1.192-2.295 2.965-2.295 4.95 0 3.59 2.909 6.5 6.5 6.5s6.5-2.91 6.5-6.5c0-1.985-.892-3.758-2.295-4.95-.447-.38-.705-.938-.705-1.525v-7.525c0-1.933-1.567-3.5-3.5-3.5zm2.107 14.718c-1.012-.89-1.607-1.734-1.607-3.22v-6.498h-1v6.498c0 1.484-.597 2.332-1.607 3.22-.794.698-1.393 1.642-1.393 2.782 0 1.933 1.567 3.5 3.5 3.5s3.5-1.567 3.5-3.5c0-1.14-.599-2.083-1.393-2.782zm-9.607-6.218c0 1.933-1.567 3.5-3.5 3.5s-3.5-1.567-3.5-3.5 1.567-3.5 3.5-3.5 3.5 1.567 3.5 3.5zm-8 0c0-.171.032-.333.051-.5h-2.051v1h2.051c-.019-.167-.051-.329-.051-.5zm1.705-3.501l-1.448-1.449-.707.707 1.448 1.448c.21-.261.445-.497.707-.706zm6.297.706l1.447-1.448-.707-.707-1.448 1.448c.263.21.498.445.708.707zm-3.502-1.705c.171 0 .334.032.5.05v-2.05h-1v2.05c.166-.018.329-.05.5-.05zm0 9c-.171 0-.334-.032-.5-.05v2.05h1v-2.05c-.166.018-.329.05-.5.05zm4.449-5c.019.167.051.329.051.5l-.051.5h2.051v-1h-2.051zm-7.951 3.294l-1.448 1.449.707.707 1.448-1.448c-.262-.21-.497-.446-.707-.708zm6.296.708l1.448 1.448.707-.707-1.447-1.448c-.21.262-.445.497-.708.707z"/></svg>
              <h2>{weather.main.humidity}%</h2>
            </div>
          </div>
          <div className="icon_2">
            <div>
              <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path d="M21.688 6.668c.172 3.71-3.595 6.901-6.56 9.735-2.175 2.079-3.849 4.743-4.03 7.597-2.059-1.342-2.509-3.797-2.123-5.938.53-2.95-2.231-4.9-3.293-5.822-2.15-1.867-3.682-4.216-3.682-6.647 0-4.607 5.533-5.593 10.108-5.593 3.397 0 9.892.913 9.892 3.798 0 1.979-5.022 3.202-9.56 3.202-8.735 0-7.32-3.886-1.765-3.886.9 0 1.821.097 2.622.323-2.927.057-6.493 1.563-1.189 1.563 1.136 0 7.163-.385 7.163-1.5s-5.303-1.578-7.163-1.578c-3.281 0-8.108.609-8.108 3.671 0 1.636 1.063 3.46 2.994 5.137l.311.262c1.29 1.076 4.307 3.591 3.646 7.389.72-1.172 1.851-2.511 2.946-3.568-.542 0-1.602-.302-2.229-.75 1.071.089 3.084-.188 4.604-1.562-1.312 0-2.787-.219-3.74-1.062 2.44.155 5.303-.312 6.282-1.854-1.625.333-4.834.104-6.702-.875 3.045.045 7.022-.462 9.578-2.079l-.001.028.003-.001-.004.01zm-7.688 15.332c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1zm-7 0c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1zm-3-1c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1zm2-2c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1zm10 0c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1zm2 2c-.553 0-1 .448-1 1s.447 1 1 1 1-.448 1-1-.447-1-1-1z"/></svg>
              <h2>{(weather.wind.gust * 1.609).toFixed(1)} km/hr</h2>
            </div>
            <div>
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 24 24"><path d="M17.5 2c.827 0 1.5.673 1.5 1.5v7.525c0 1.569.514 2.287 1.411 3.05 1.01.858 1.589 2.106 1.589 3.425 0 2.481-2.019 4.5-4.5 4.5s-4.5-2.019-4.5-4.5c0-1.319.579-2.567 1.59-3.425.896-.761 1.41-1.479 1.41-3.05v-7.525c0-.827.673-1.5 1.5-1.5zm0-2c-1.933 0-3.5 1.567-3.5 3.5v7.525c0 .587-.258 1.146-.705 1.525-1.403 1.192-2.295 2.965-2.295 4.95 0 3.59 2.909 6.5 6.5 6.5s6.5-2.91 6.5-6.5c0-1.985-.892-3.758-2.295-4.95-.447-.38-.705-.938-.705-1.525v-7.525c0-1.933-1.567-3.5-3.5-3.5zm2.107 14.718c-1.012-.89-1.607-1.734-1.607-3.22v-6.498h-1v6.498c0 1.484-.597 2.332-1.607 3.22-.794.698-1.393 1.642-1.393 2.782 0 1.933 1.567 3.5 3.5 3.5s3.5-1.567 3.5-3.5c0-1.14-.599-2.083-1.393-2.782zm-9.607-6.218c0 1.933-1.567 3.5-3.5 3.5s-3.5-1.567-3.5-3.5 1.567-3.5 3.5-3.5 3.5 1.567 3.5 3.5zm-8 0c0-.171.032-.333.051-.5h-2.051v1h2.051c-.019-.167-.051-.329-.051-.5zm1.705-3.501l-1.448-1.449-.707.707 1.448 1.448c.21-.261.445-.497.707-.706zm6.297.706l1.447-1.448-.707-.707-1.448 1.448c.263.21.498.445.708.707zm-3.502-1.705c.171 0 .334.032.5.05v-2.05h-1v2.05c.166-.018.329-.05.5-.05zm0 9c-.171 0-.334-.032-.5-.05v2.05h1v-2.05c-.166.018-.329.05-.5.05zm4.449-5c.019.167.051.329.051.5l-.051.5h2.051v-1h-2.051zm-7.951 3.294l-1.448 1.449.707.707 1.448-1.448c-.262-.21-.497-.446-.707-.708zm6.296.708l1.448 1.448.707-.707-1.447-1.448c-.21.262-.445.497-.708.707z"/></svg>
              <h2>{weather.main.pressure} mb</h2>
            </div>
          </div>
        </div>
      ) : (
          <div className="load">Loading...</div>
      )}
    </div>
  );
};

export default App;
